<template>
  <div class="data">
    <vx-card class="mb-5"> 
      <div class="md:flex">
          <div class="lg:w-1/3 sm:w-full mt-4 md:mt-0">
            <v-select  :disabled="disabled" :placeholder="'-- Pilih Entitas --'" label="nama" :options="city" v-model="citySelected"  ></v-select>
          </div>
          <div class="lg:w-1/4 sm:w-full mt-4 md:mt-0 md:ml-6">
            <datepicker :minimumView="'month'" :maximumView="'year'" :format="'MMM yyyy'" v-model="selectedMonth" :disabled="disabled"></datepicker>
          </div>
          <div class="mt-4 md:mt-0 md:ml-6">
            <vs-button :disabled="disabled" radius color="primary" icon="search" v-on:click="loadDataServer"  style="display:inline; margin-right:30px;"></vs-button>
            <vs-button :disabled="excell" @click="tableToExcel('table', 'Neraca')" type="border"><i class="feather icon-file-text"></i></vs-button>
          </div>   
      </div>
    </vx-card>

    <vx-card v-show="table.length > 0">
      <div hidden ref="table" id="loremTable" summary="lorem ipsum sit amet" rules="groups" frame="hsides">
        <h4>Bulan : {{selectedMonth | moment("MMMM YYYY") }}</h4>
        <table>
          <thead>
            <tr class="bg-primary text-white">
              <th>Group</th>
              <th>Akun</th>
              <th>Keterangan</th>
              <th class="text-center">Jumlah</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(item ,index)  in (arrayTreeObj)" :key="index" v-bind:class="[(item.child!= null ) ? 'greyLine':'']">
              <td>
                {{item.group}}
              </td>
              <td data-th="ID">
                <span v-bind:style="setPadding(item)">{{item.id}}</span>
              </td>
              <td data-th="Name" @click="toggle(item)">
                <span v-bind:style="setPadding(item)">                
                  <vs-icon  style="cursor: pointer;" :icon="iconName(item)" color="primary"  ></vs-icon>
                  {{item.desc}}
                </span>
              </td>
              <td class="text-right" width="20%" data-th="Account">
                <div v-if="item.editable == false">
                  <span>{{item.amount}}</span>
                </div>
                <div v-else>
                  <span>{{item.amount}} </span>
                </div>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <table class="w-full table table-responsive mt-4">
        <thead>
          <tr class="bg-primary text-white">
            <th>Group</th>
            <th>Akun</th>
            <th>Keterangan</th>
            <th class="text-center">Jumlah</th>
          </tr>
        </thead>
        <tbody >
          <tr v-for="(item ,index)  in (arrayTreeObj)" :key="index" v-bind:class="[(item.child!= null ) ? 'greyLine':'']">
            <td>
              {{item.group}}
            </td>
            <td data-th="ID">
              <span v-bind:style="setPadding(item)"> 
              {{item.id}}
              </span>
            </td>
            <td data-th="Name" @click="toggle(item)">
              <span v-bind:style="setPadding(item)">                
              <vs-icon  style="cursor: pointer;" 
                  :icon="iconName(item)" color="primary"  ></vs-icon>
                  {{item.desc}}
              </span>
            </td>
            <td class="text-right" width="20%" data-th="Account">
              <div v-if="item.editable == false">
                <imoney v-model="item.amount" disabled :readonly="true" @keyup.enter="getNilai" ></imoney>
              </div>
              <div v-else>
                <imoney v-model="item.amount"  @afterUpdate="getNilai()" @keyup.enter.native="next"  class="child"></imoney>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right mt-10" v-show="button == true">
        <vs-button  class="mr-3" color="primary" v-on:click="add">Kirim</vs-button>
          <form  @submit.prevent="clear" style="display:inline">
          <vs-button color="danger" v-on:click="clear">Batal</vs-button>
        </form>
      </div>
    </vx-card>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import imoney from '../components/InputMoney'

export default {
  data() {
    return {
      uri :'data:application/vnd.ms-excel;base64,',
      template:'<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40"><head><!--[if gte mso 9]><xml><x:ExcelWorkbook><x:ExcelWorksheets><x:ExcelWorksheet><x:Name>{worksheet}</x:Name><x:WorksheetOptions><x:DisplayGridlines/></x:WorksheetOptions></x:ExcelWorksheet></x:ExcelWorksheets></x:ExcelWorkbook></xml><![endif]--></head><body><table>{table}</table></body></html>',
      base64: function(s){ return window.btoa(unescape(encodeURIComponent(s))) },
      format: function(s, c) { return s.replace(/{(\w+)}/g, function(m, p) { return c[p]; }) },
      uid:this.$store.state.AppActiveUser.uid,
      isExpanded: true,
      selectedRowID: {},
      table: [],
      itemId: null,
      selectedMonth:new Date(),
      citySelected: null,
      city:[],
      nilai: '',
      updateSubmit: false,
      popupActivo: false,
      disabled: false,
      button: false,
      excell:true,
    }
  },

  components: {
    Datepicker,
    imoney
  },
  
  mounted() {
    this.loadEntitas()
    this.loadDataLocal()
  },

  methods: {
    tableToExcel(table, name) { 
      if (!table.nodeType) table = this.$refs.table
      const tableClean = table.innerHTML.replace(/done|remove_circle_outline|/g,'')
      const ctx = {worksheet: name || 'Worksheet', table: tableClean}
      window.location.href = this.uri + this.base64(this.format(this.template, ctx))
    },

    loadEntitas() {
      this.city = this.$store.state.AppActiveUser.entitas
    },

    getNilai() {
      this.sumRecrusive(this.table);
    },

    next() {
      const inputs = Array.from(event.target.form.querySelectorAll('.child'));
      const index = inputs.indexOf(event.target);
      if (index < inputs.length) {
        inputs[index + 1].focus();
      }
    },

    loadDataLocal() {
      let localData = JSON.parse(localStorage.getItem('neraca_'+this.uid));
     
      if (localData != null ) {
        this.$swal.fire({
          title: 'Apakah Anda Yakin ?',
          text: "Ingin Melanjutkan Data Yang Sebelumnya",
          icon: 'warning',
          showCancelButton: true,
          confirmButtonColor: '#3085d6',
          cancelButtonColor: '#d33',
          confirmButtonText: 'Ya'
        })
        .then((result) => {
          if (result.value) { 
            this.citySelected = localData.citySelected
            this.selectedMonth = new Date(localData.selectedMonth)
            this.table = localData.table
            this.disabled = true
            this.button = true
            this.excell = false
          }
        })
      }
    },

    loadDataServer() {
      if (this.citySelected== null) {
        this.$swal.fire(
          'Oopss !',
          'Anda Belum Memilih Entitas',
          'error'
        )
      } else {
        this.$vs.loading()
        var dataParam = btoa(this.selectedMonth)
        var kodeParam = btoa(this.citySelected.kode)
      
        this.$http.get('neraca/'+ dataParam+'/'+ kodeParam)
          .then((res)=>{
            this.table = res.data.message[0].account
            this.sumRecrusive(this.table);
            this.disabled = true
            this.button = true
            this.excell = false
          })
          .catch(err=>{
            this.$vs.loading.close()
            this.$swal.fire(
              'Oopss !',
              err.response.data.message,
              'error'
            )
          })
          .finally(() => {setTimeout( ()=> {
              this.$vs.loading.close()
          }, 2000);});
      }
    }, 
  
    add() {
      this.addToLocal()
      this.addToServer()
    },

    addToLocal() {
      let postData = {
        selectedMonth: this.selectedMonth,
        citySelected: this.citySelected,
        table: this.table
      };
      localStorage.setItem('neraca_'+this.uid, JSON.stringify(postData))
    },

    addToServer() {
      let year = new Intl.DateTimeFormat('id', { year: 'numeric' }).format(this.selectedMonth)
      let month = new Intl.DateTimeFormat('id', { month: 'long' }).format(this.selectedMonth)
      
      this.$swal.fire({
        title: '<h3><strong>Apakah Anda Yakin Melanjutkan ?</strong></h3>',
        html: '<span class="text-center">Untuk Mengirimkan Data Bulan <strong>'+ month +''+year+'</strong> Entitas <strong>'+this.citySelected.nama+'</strong></span>',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Ya'
      })
      .then((result) => {
        if (result.value) {
          let postData = {
              bulan: this.selectedMonth,
              entitas: this.citySelected,
              data: this.table,
              aksi: 'CEK'
            };
        
            this.$http.post('neraca', postData)
            .then((response)=>{
              if(response.status == 200){
                this.$swal.fire({
                  html: response.data.message,
                  width: 650,
                  icon: 'warning',
                  showCancelButton: true,
                  confirmButtonColor: '#3085d6',
                  cancelButtonColor: '#d33',
                  confirmButtonText: 'Simpan'
                })
                .then((result) =>{
                  if (result.value) {
                    let postData = {
                      bulan: this.selectedMonth,
                      entitas: this.citySelected,
                      data: this.table,
                      aksi: 'SIMPAN'
                    };
                    this.$http.post('neraca', postData)
                    .then((response)=>{
                      if (response.status == 200){
                        localStorage.removeItem('neraca_'+this.uid)
                        this.$swal.fire(
                          'Success !',
                          response.data.message,
                          'success'
                        )
                        this.reset()
                      } else {
                        this.$swal.fire(
                          'Gagal !',
                          'Data Tidak Jadi Disimpan',
                          'error'
                        )
                      }
                    })
                  } else {
                    this.$swal.fire(
                      'Gagal !',
                      'Tidak Jadi Disimpan',
                      'error'
                    )
                  }
                })
              }  
            })
            .catch(err=>{
              this.$vs.loading.close()
              this.$swal.fire(
                'Gagal !',
                err.response.data.message,
                'error'
              )
            })
        } else {
          this.button = true
        }
      })
      .catch(err=>{
        this.$vs.loading.close()
        this.$swal.fire(
          'Gagal !',
          err.response.data.message,
          'error'
        )
      })
    },
        
    clear() {
      this.$swal.fire({
        title: 'Apakah Yakin Ingin Batal ?',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Ya'
      })
      .then((result) => {
        if (result.value) {
          this.reset()
          this.$swal.fire({
            title: 'Success',
            text: 'Data Yang Anda Input Berhasil Di Batalkan',
            icon: 'success',
            timer: 1500,
            showCancelButton: false,
            showConfirmButton: false
          })
        } else {
          this.button = true
        }
      })
    },

    reset(){
      this.citySelected= null
      this.selectedMonth= new Date()
      this.table= []
      this.$validator.reset()
      this.disabled = false
      this.button = false
      this.excell = true
      // location.reload()
    },

    sumRecrusive(obj) {
      let vm = this;
      var total = 0 ;
      obj.forEach(function(o) {
        if (o.child && o.child.length != 0) {
          o.amount = vm.sumRecrusive(o.child);
          total += o.amount;
        }else{
          if(!isNaN(o.amount))
            total += parseFloat(o.amount);
        }
      });
      return total;
    },

    recursive(obj, newObj, level, itemId, isExpend) {
      let vm = this;
      var total = 0 ;
      obj.forEach(function(o) {
        if (o.child && o.child.length != 0) {
          o.level = level;
          o.leaf = false;
          newObj.push(o);
          if (o.id == itemId) {
            o.expend = isExpend;
          }
          if(o.expend == undefined){
            o.expend =true
          }
          if (o.expend == true) {
            vm.recursive(o.child, newObj, o.level + 1, itemId, isExpend);
          }
        } else {
          o.level = level;
          o.leaf = true;
          newObj.push(o);
          return false;
        }
      });
      return total;
    },

    iconName(item) {
      if (item.expend == true) {
        return "remove_circle_outline";
      }

      if (item.child && item.child.length > 0) {
        return "control_point";
      }

      return "done";
    },

    toggle(item) {
      let vm = this;
      vm.itemId = item.id;
      vm.selectedRowID =item.id;
      item.leaf = false;
      //show  sub items after click on + (more)
      if (
        item.leaf == false &&
        item.expend == false &&
        item.child != undefined
      ) {
        if (item.child.length != 0) {
          vm.recursive(item.child, [], item.level + 1, item.id, true);
        }
      }


      if (item.expend == true && item.child != undefined) {
        // var __subindex = 0;
        item.child.forEach(function(o) {
          o.expend = false;
        });

        vm.$set(item, "expend", false);
        vm.$set(item, "leaf", false);
        vm.itemId = null;
      }
    },

    setPadding(item) {
      return `padding-left: ${item.level * 20}px;`;
    },
  },

  computed: {
    arrayTreeObj() {
      let vm = this;
      var newObj = [];
      vm.recursive(vm.table, newObj, 0, vm.itemId, vm.isExpanded);
      return newObj;
    }
  },

  watch: {
    selectedIDRow(val) {
      if (this.selectedRowID != null) {
        this.$set(this.selectedRowID, "id", val);
      }
    }
  },
}
</script>
  
<style scoped>
.vs-input{
  border:none
}

table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
  border: 1px solid #ddd;
}

th, td {
  text-align: left;
  padding: 10px;
}

.greyLine {
  background-color: #f2f2f2;
}
</style>