<template>
    <input type="text"
         :value="formattedValue"
         onfocus="this.select();"
         @keypress="isNumber($event)"
         v-on:input="updateValue($event)"
         style="text-align:right;" 
         class="vs-inputx vs-input--input normal mt-2"
       />
</template>

<script>

export default {
  name: 'Money',
  props: {
    value: {
      required: true,
      type: [Number, String],
      default: 0
    },
  },
  data () {
    return {
      formattedValue: '',
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (newValue) {
        //console.log('real value:'+newValue)
        var unfromatted = this.unformated(newValue)
        var formatted = this.format(unfromatted) 
        if (formatted !== this.formattedValue) {
          this.formattedValue = formatted
        }
      }
    }
  },
  methods: {

    updateValue (evt) {
      var newValue = this.unformated(evt.target.value)
      if(newValue === ''){
        newValue = 0
      }
      this.$emit('input',newValue)
      this.$emit('afterUpdate')
    },
    isNumber(evt) {
      evt = (evt) ? evt : window.event;
      var charCode = (evt.which) ? evt.which : evt.keyCode;
      if ((charCode > 31 && (charCode < 48 || charCode > 57)) && charCode !== 46 && charCode !== 45) {
        evt.preventDefault();
      } else {
        //prevent multiple
        if(charCode === 46){
          if(this.formattedValue.toString().indexOf('.') > -1){
            evt.preventDefault();
          }
        }
        return true;
      }
    },
    format(value){
      return value.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",")
    },
    unformated(value){
      return value.toString()
        .replace(/[^0-9.-]/g, '')       // remove chars except number, hyphen, point. 
        .replace(/(\..*)\./g, '$1')     // remove multiple points.
        .replace(/(?!^)-/g, '')         // remove middle hyphen.
        .replace(/^0+(\d)/gm, '$1'); 
    }

  }
}
</script>